.comment-block {
  margin-bottom: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid #eee;
  &:last-child {
    border-bottom: 0;
  }
}

.comment-people {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  img {
    max-width: 40px;
    max-height: 40px;
    margin-right: 15px;
  }
  .name {
    font-weight: 600;
    color: #343a40;
  }
}
